import React from 'react';
import './Cards.css';

function Cards() {
    return (
        <div className="card-container">
            <div className="box1">
            <img src="/images/Plitvice.jpg" alt="" />
               
            </div>
            <div className="box2">
                <h3>Box Two</h3>
                <p className="p1">Plitvice Lakes is the oldest and largest national park in the Republic of Croatia.
                    The park is situated in the mountainous region of Croatia,
                     between the Mala Kapela mountain range in the west and northwest,
                      and the Lička Plješivica mountain range to the southeast.
                      
             </p>
            </div>
            
        </div>
        
        
    )
}

export default Cards
